type SelectedType = {
    items:ItemType[]
}

type ItemType = {
    title:string,
    value:any
}

const editMode =()=> {

}

export const Selected = (props:SelectedType)=>{
   return (
       <div onBlur={editMode}>{props.items.map((i)=>{
           return(
               <select><option>{i.title}</option></select>
           )
       })}</div>
   )
}